html {
  box-sizing: border-box;
  -ms-overflow-style: scrollbar;
  height: 100%;
}

body {
  height: 100%;
  padding: 0;
  margin: 0;
}

.grid-container {
  display: grid;
  grid-gap: 15px;
  grid-template-columns: repeat(12, minmax(0, 1fr));
  grid-template-rows: auto;
  margin-right: auto;
  margin-left: auto;
  display: grid;
  grid-gap: 12px;
  grid-template-columns: repeat(1, minmax(0, 1fr));
  grid-template-rows: auto;
  margin-right: auto;
  margin-left: auto;
}

@media (min-width: 480px) {
  .grid-container {
    max-width: 640px;
  }
}

@media (min-width: 960px) {
