 */
.--toggler-menu {
  display: none;
}

.--has-navbar-xl .--navbar {
  position: fixed;
  top: 0;
  left: -300px;
  width: 300px;
  height: 100vh;
  transition: left .25s ease-in-out;
}

.--has-navbar-xl .--navbar.--open {
  left: 0;
  z-index: 1000;
}

.--has-navbar-xl .--toggler-menu {
  display: inline-block;
}

.--overlay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  width: 100vw;
  height: 100vh;
  visibility: hidden;
  background: rgba(0, 0, 0, 0.4);
  opacity: 0;
  transition: all .25s ease-in-out;
}

.--overlay.--visible {
  visibility: visible;
  opacity: 1;
}

